@import '~assets/styles/h3Imports';

.root {
  background: var(--white);
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  margin-top: var(--spacing-xxxs);
  padding: 16px;
  position: absolute;
  width: 100%;
  z-index: 2;

  [data-element='under-length'] {
    display: flex;

    [aria-hidden='true'] {
      flex: 0 1 16px;
      margin-right: 8px;
      position: relative;
      top: 2px;
    }

    p {
      margin: 0;
      @include paragraph-small;
    }
  }
}

.list {
  list-style: none;
  margin: 8px -16px 0;
  padding: 0;

  [data-element='result'] {
    margin: 0;
    padding: 0;

    &:nth-child(even) {
      background-color: var(--interface-info-background);
    }
  }
}

.narrow {
  @include caption-text;

  border-bottom: 1px solid rgba(#323264, 0.16);
  color: var(--gray-3);
  margin: 0;
  padding-bottom: var(--spacing-xxxs);
}

.see-all {
  border-top: 1px solid rgba(#323264, 0.16);
  padding-top: var(--spacing-xxxs);

  [data-element='show-all'] {
    background: none;
    border: none;
    @include link-text;
  }
}
